import React from 'react';
import Card from './Cards';
import Arrays from './Arrays';

const App = () => {
  return (
    <>
      <h1 className="mainhead">List of Top 5 Netflix Series in 2021.</h1>
      <div className="after_root">
        {Arrays.map((val) => {
          return (
            <Card
              key={val.id}
              imgsrc={val.imgsrc}
              title={val.title}
              sname={val.sname}
              link={val.link}
            />
          );
        })}
      </div>
    </>
  );
}

export default App;