function Card(props) {
    return (
        <>
            <div className="bigcard">
                <img src={props.imgsrc} alt="Error" className="cardimg" />
                <div className="dtcard">
                    <span className="cardspan">{props.title}</span>
                    <h3 className="cardhead">{props.sname}</h3>
                    <a href={props.link} target="_blank">
                        <button className="button">Watch Now</button>
                    </a>
                </div>
            </div>
        </>
    );
}

export default Card;
